export const isModeDevelopment = import.meta.env.MODE === 'development';
export const isModeProduction = import.meta.env.MODE === 'production';
export const urlBuilder = (url = '') => {
    if(url.length === 0) url = '/images/teachers/81fceae724d5e3d75fd45bda1a4cd79d.jpg';
    if (isModeDevelopment)
        return 'https://35school.flyadmin.ru'
        + (url.split('/')?.[0] !== '' ? '/' : '')
            + url;
    return url;
}